<!--
Monitoraggio quadrature ARCA
Viene mostrata una lista dei flussi FE e del loro stato per l'invio a 6 giorni
-->
<template>
  <div class="container">
    <div class="form-group">
      <label for=""></label>
      <select v-model="selected" class="form-control" name="" id="">
        <option value="null" selected disabled>-- Seleziona un'azienda --</option>
        <option value="arca">Arca</option>
        <option value="comieco">Comieco</option>
        <option value="conai">Conai (Gruppo)</option>
      </select>
    </div>
    <div v-if="selected != null">
      <h6 class="text-center">Totale flussi: {{ flussi.total }}</h6>
      <!-- Tabella principale -->
      <table class="table table-striped table-sm">
        <thead>
          <tr>
            <th>Nome flusso</th>
            <th>Data flusso</th>
            <th>Esito</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(flusso, i) in flussi.tracciati"
            :key="flusso.Identificativo_Tracciato"
            :class="{ 'table-danger': flusso.status.esito != 'success' }"
          >
            <td>{{ flusso.Identificativo_Tracciato }}</td>
            <td>{{ flusso.DataInsert }}</td>
            <td>
              <span v-if="flusso.status.messaggio != undefined">{{ flusso.status.messaggio }}</span>
              <p v-if="flusso.status.file_in_zip != undefined">
                File nello zip: {{ flusso.status.file_in_zip }}
                <br />
                File in ISC: {{ flusso.status.file_isc }}
                <br />
                File tabella 6gg: {{ flusso.status.file_sei_giorni }}
              </p>
              <div class="float-right">
                <button
                  class="btn btn-sm btn-light"
                  @click="getDettagliFlusso(flusso.Identificativo_Tracciato, i)"
                  v-if="flusso.status.esito != 'success' && flusso.status.file_in_zip == undefined"
                >
                  <i class="fa fa-plus" aria-hidden="true"></i> Dettagli
                </button>
                <button
                  class="btn btn-sm btn-light"
                  @click="sanaFlusso(flusso.Identificativo_Tracciato, i)"
                  v-if="flusso.status.esito != 'success'"
                >
                  <i class="fa fa-check" aria-hidden="true"></i> Sana
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <!-- Tabella principale /-->

      <!-- Paginazione -->
      <nav>
        <ul class="pagination justify-content-center">
          <li class="page-item" :class="{ disabled: page - 1 < 1 }">
            <a class="page-link" href="#" @click.prevent="page = 1">
              <span aria-hidden="true">&laquo;</span>
            </a>
          </li>
          <li class="page-item">
            <a class="page-link" href="#" @click.prevent="page -= 1">
              <span aria-hidden="true">&lt;</span>
            </a>
          </li>

          <li class="page-item disabled">
            <a class="page-link" href="#" @click.prevent>
              <span aria-hidden="true">Pagina {{ page }}/{{ flussi.pages || '...' }}</span>
            </a>
          </li>

          <li class="page-item">
            <a class="page-link" @click.prevent="page += 1" href="#">
              <span aria-hidden="true">&gt;</span>
            </a>
          </li>

          <li class="page-item" :class="{ disabled: page + 1 > flussi.pages }">
            <a class="page-link" href="#" @click.prevent="page = flussi.pages">
              <span aria-hidden="true">&raquo;</span>
            </a>
          </li>
        </ul>
      </nav>
      <!-- Paginazione /-->
    </div>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      selected: null,
      /** @prop array lista dei flussi visualizzati */
      flussi: [],
      /** @prop int pagina corrente */
      page: 1
    };
  },
  // mounted verrà eseguita al caricamento della view
  mounted: function() {},
  methods: {
    /**
     * Recupera i flussi dal backend
     */
    getFlussi: function(company, page) {
      this.$store.dispatch("loading");
      this.axios
        .get(this.apiURL + "/fe/wiit/quadrature/" + company + "/list/" + page)
        .then(r => {
          this.flussi = r.data;
          this.$store.dispatch("loaded");
        });
    },
    /**
     * Ottiene i dettagli sui conteggi del flusso
     */
    getDettagliFlusso: function(company, flusso, i) {
      this.$store.dispatch("loading");
      this.axios
        .get(this.apiURL + "/fe/wiit/quadrature/" + company + "/dettagli/" + flusso)
        .then(r => {
          this.$set(this.flussi.tracciati[i], "status", r.data);
          this.$store.dispatch("loaded");
        });
    },
    /**
     * Lancia la sanatura dei conteggi del flusso
     * (scrittura tabella a Sei Giorni dei record mancanti)
     */
    sanaFlusso: function(company, flusso, i) {
      this.$store.dispatch("loading");
      this.axios.post(this.apiURL + "/fe/wiit/quadrature/" + company + "/sana/" + flusso).then(r => {
        this.$set(this.flussi.tracciati[i], "status", r.data);
        this.$store.dispatch("loaded");
      });
    }
  },
  // Qui viene descritto il comportamento dell'applicativo al cambio delle variabili specificate
  watch: {
    selected: function() {
      this.page = 1;
      this.getFlussi(this.selected, this.page);
    },
    page: function() {
      this.getFlussi(this.selected, this.page);
    }
  }
};
</script>